.title-text {
  margin-bottom: 4rem;
}

.title-text_container {
  max-width: 815px;
  margin: 0 auto;
}

.title-text_container .title-container {
  margin-bottom: 3rem;
}

.title-text_container .title-container h2 {
  color: #E8B44A;
  font-weight: 700;
  font-size: 28px;
  line-height: 40px;
  letter-spacing: 1px;
}

.title-text_container .title-container h4 {
  color: #17314F;
  font-weight: 400;
  font-size: 20px;
  line-height: 32px;
}

.title-text_container .text-container {
  color: #091626;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
}

.title-text_container .text-container p {
  color: #091626;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
}

@media (min-width: 992px) {
  .title-text_container .title-container h2 {
    font-size: 56px;
    line-height: 72px;
  }
  .title-text_container .title-container h4 {
    font-size: 24px;
    line-height: 32px;
  }
}